body, h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

.page {
  scroll-behavior: smooth;
  background: #1d1d1d;
  font-family: Poppins, sans-serif;
}

.page__body {
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: scroll;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin-bottom: 90px;
}

@media screen and (min-width: 744px) and (max-width: 1023px) {
  .page__section {
    margin-bottom: 120px;
  }
}

@media (min-width: 1024px) {
  .page__section {
    margin-bottom: 160px;
  }
}

.page__section-title {
  text-align: center;
  letter-spacing: -1.5px;
  color: #fff;
  margin-bottom: 48px;
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
}

@media screen and (min-width: 744px) and (max-width: 1023px) {
  .page__section-title {
    font-size: 48px;
    line-height: 100%;
  }
}

@media (min-width: 1024px) {
  .page__section-title {
    font-size: 64px;
    line-height: 100%;
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 744px) {
  .container {
    max-width: 1020px;
    padding: 0 40px;
  }
}

.grid {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  display: grid;
}

@media screen and (min-width: 744px) and (max-width: 1023px) {
  .grid {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }
}

@media (min-width: 1024px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }
}

.grid--desktop {
  display: block;
}

@media (min-width: 744px) {
  .grid--desktop {
